<template>
  <q-input
    :model-value="modelValue"
    :label="label"
    :placeholder="placeholder"
    :type="type"
    :color="color"
    :bg-color="bgColor"
    :outlined="outlined"
    :borderless="borderless"
    :standout="standout"
    :rounded="rounded"
    :square="square"
    :loading="loading"
    :clearable="clearable"
    :clear-icon="clearIcon"
    :disable="disable"
    :readonly="readonly"
    :autofocus="autofocus"
    :error="error"
    :error-message="errorMessage"
    :hint="hint"
    :prefix="prefix"
    :suffix="suffix"
    :mask="mask"
    :rules="rules"
    :validate-on-blur="validateOnBlur"
    :maxlength="maxlength"
    :counter="counter"
    :bottom-slots="bottomSlots"
    @update:model-value="updateValue"
    @blur="onBlur"
    @focus="onFocus"
    @clear="onClear"
  >
    <!-- Left section slot -->
    <template v-if="$slots.prepend" #prepend>
      <slot name="prepend" />
    </template>

    <!-- Right section slot -->
    <template v-if="$slots.append" #append>
      <slot name="append" />
    </template>

    <!-- Before input slot -->
    <template v-if="$slots.before" #before>
      <slot name="before" />
    </template>

    <!-- After input slot -->
    <template v-if="$slots.after" #after>
      <slot name="after" />
    </template>
  </q-input>
</template>

<script lang="ts">
import { defineComponent, PropType } from 'vue';
import { QInput } from 'quasar';

export default defineComponent({
  name: 'MyInputText',
  components: {
    QInput,
  },
  props: {
    // Model value
    modelValue: { type: [String, Number], default: '' },

    // Basic input properties
    label: { type: String, default: undefined },
    placeholder: { type: String, default: undefined },
    type: {
      type: String as PropType<
        'text' | 'password' | 'textarea' | 'email' |
        'search' | 'tel' | 'file' | 'number' | 'url'
      >,
      default: 'text'
    },

    // Styling properties
    color: { type: String, default: 'primary' },
    bgColor: { type: String, default: undefined },
    outlined: { type: Boolean, default: false },
    borderless: { type: Boolean, default: false },
    standout: {
      type: [Boolean, String] as PropType<boolean | string>,
      default: false
    },
    rounded: { type: Boolean, default: false },
    square: { type: Boolean, default: false },

    // State properties
    loading: { type: Boolean, default: false },
    clearable: { type: Boolean, default: false },
    clearIcon: { type: String, default: undefined },
    disable: { type: Boolean, default: false },
    readonly: { type: Boolean, default: false },
    autofocus: { type: Boolean, default: false },

    // Validation properties
    error: { type: Boolean, default: false },
    errorMessage: { type: String, default: undefined },
    hint: { type: String, default: undefined },

    // Addon properties
    prefix: { type: String, default: undefined },
    suffix: { type: String, default: undefined },

    // Advanced properties
    mask: { type: String, default: undefined },
    rules: {
      type: Array as PropType<Array<(val: string) => boolean | string>>,
      default: () => []
    },
    validateOnBlur: { type: Boolean, default: false },
    maxlength: { type: [Number, String], default: undefined },
    counter: { type: Boolean, default: false },
    bottomSlots: { type: Boolean, default: false },
  },
  emits: [
    'update:model-value',
    'blur',
    'focus',
    'clear'
  ],
  setup(props, { emit }) {
    const updateValue = (value: string | number | null) => {
      emit('update:model-value', value);
    };

    const onBlur = (evt: FocusEvent) => {
      emit('blur', evt);
    };

    const onFocus = (evt: FocusEvent) => {
      emit('focus', evt);
    };

    const onClear = () => {
      emit('clear');
    };

    return {
      updateValue,
      onBlur,
      onFocus,
      onClear,
    };
  }
});
</script>

<style lang="scss" scoped>
.my-input {
  font-family: var(--font-family, inherit);
  transition: all 0.3s ease;
}
</style>
