<template>
  <div
    class="ds-card"
    :class="{
      'ds-card--rounded': rounded,
      'ds-card--with-image': imageUrl
    }"
  >
    <img
      v-if="imageUrl"
      :src="imageUrl"
      :alt="imageAlt || title"
      class="ds-card__image"
    />

    <div class="ds-card__content">
      <span v-if="date" class="ds-card__date">
        {{ formatDate(date) }}
      </span>

      <h3 class="ds-card__title">{{ title }}</h3>

      <p class="ds-card__excerpt">{{ excerpt }}</p>

      <MyButton
        v-if="ctaLabel"
        :label="ctaLabel"
        :variant="ctaVariant"
        :color="ctaColor"
        class="ds-card__cta"
        @click="$emit('cta-click')"
      />
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from 'vue';
import MyButton from '../Button/Button.vue';

export default defineComponent({
  name: 'MyCard',
  components: {
    MyButton
  },

  props: {
    imageUrl: {
      type: String,
      default: '',
    },
    imageAlt: {
      type: String,
      default: '',
    },
    date: {
      type: [Date, String],
      default: null,
    },
    title: {
      type: String,
      required: true,
    },
    excerpt: {
      type: String,
      required: true,
    },
    ctaLabel: {
      type: String,
      default: '',
    },
    ctaVariant: {
      type: String,
      default: 'solid',
    },
    ctaColor: {
      type: String,
      default: 'primary',
    },
    rounded: {
      type: Boolean,
      default: true,
    },
  },

  emits: ['cta-click'],

  setup() {
    const formatDate = (date: Date | string) => {
      const d = new Date(date);
      return new Intl.DateTimeFormat('en-US', {
        month: 'short',
        day: 'numeric',
        year: 'numeric',
        hour: 'numeric',
        minute: 'numeric',
        hour12: true,
      }).format(d);
    };

    return {
      formatDate,
    };
  },
});
</script>

<style lang="scss">
.ds-card {
  background: white;
  border: 1px solid var(--ds-border-color, rgba(0, 0, 0, 0.12));
  overflow: hidden;
  transition: box-shadow 0.3s ease;

  &--rounded {
    border-radius: var(--ds-border-radius, 8px);
  }

  &:hover {
    box-shadow: var(--ds-shadow-md, 0 4px 6px -1px rgba(0, 0, 0, 0.1));
  }

  &__image {
    width: 100%;
    object-fit: cover;
  }

  &__content {
    padding: 1.5rem;
  }

  &__date {
    display: block;
    font-size: 0.875rem;
    color: var(--ds-color-text-secondary, #666);
    margin-bottom: 0.5rem;
  }

  &__title {
    font-size: 1.25rem;
    font-weight: 600;
    color: var(--ds-color-text-primary, #333);
    margin: 0 0 0.75rem 0;
  }

  &__excerpt {
    font-size: 1rem;
    color: var(--ds-color-text-secondary, #666);
    margin: 0 0 1.5rem 0;
    line-height: 1.5;
  }

  &__cta {
    margin-top: auto;
  }
}
</style>
