<!-- src/components/Skeleton/Skeleton.vue -->
<template>
    <div
      :class="[
        'skeleton-loader',
        className
      ]"
    >
      <div
        v-for="(line, index) in lines"
        :key="index"
        class="skeleton-line"
        :style="{
          width: getLineWidth(index),
        }"
      />
    </div>
  </template>

  <script lang="ts">
  import { defineComponent } from 'vue';

  export default defineComponent({
    name: 'MySkeleton',
    props: {
      lines: {
        type: Number,
        default: 3,
        validator: (value: number) => value > 0 && value <= 10
      },
      className: {
        type: String,
        default: ''
      },
      speed: {
        type: String,
        default: 'normal',
        validator: (value: string) => ['slow', 'normal', 'fast'].includes(value)
      }
    },
    setup(props) {
      const getLineWidth = (index: number): string => {
        // First line is always 70%
        if (index === 0) return '70%';

        // Last line is always 40%
        if (index === props.lines - 1) return '40%';

        // Other lines alternate between 100% and 80%
        return index % 2 === 0 ? '100%' : '80%';
      };

      return {
        getLineWidth
      };
    }
  });
  </script>

  <style lang="scss" scoped>
  .skeleton-loader {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  .skeleton-line {
    height: 20px;
    background: #eee;
    border-radius: 4px;
    animation: shimmer 2s infinite linear;
    background: linear-gradient(
      90deg,
      rgba(238, 238, 238, 1) 0%,
      rgba(245, 245, 245, 1) 50%,
      rgba(238, 238, 238, 1) 100%
    );
    background-size: 200% 100%;
  }

  @keyframes shimmer {
    0% {
      background-position: 200% 0;
    }
    100% {
      background-position: -200% 0;
    }
  }

  // Animation speed variations
  :host([speed="slow"]) .skeleton-line {
    animation-duration: 3s;
  }

  :host([speed="normal"]) .skeleton-line {
    animation-duration: 2s;
  }

  :host([speed="fast"]) .skeleton-line {
    animation-duration: 1.5s;
  }
  </style>
