<template>
  <q-btn
    :label="label"
    :color="mapVariantToQuasar"
    :flat="variant === 'text'"
    :outline="variant === 'outlined'"
    :size="mapSizeToQuasar"
    :icon="startIcon"
    :icon-right="endIcon"
    :loading="loading"
    :disabled="disabled"
    class="my-button"
    :class="[
      `my-button--${variant}`,
      `my-button--${size}`,
      className
    ]"
    @click="(evt: Event) => onClick(evt as MouseEvent)"
  >
    <slot name="startIcon"></slot>
    <slot>{{ label }}</slot>
    <slot name="endIcon"></slot>
  </q-btn>
</template>

<script lang="ts">
import { defineComponent, computed } from 'vue';
import type { PropType } from 'vue';
import { QBtn } from 'quasar';

export type ButtonVariant = 'solid' | 'outlined' | 'text';
export type ButtonSize = 'small' | 'medium' | 'large';
export type ButtonColor = 'primary' | 'secondary' | 'success' | 'error' | 'warning' | 'info';

export default defineComponent({
  name: 'MyButton',
  components: { QBtn },
  props: {
    label: {
      type: String,
      default: '',
    },
    variant: {
      type: String as PropType<ButtonVariant>,
      default: 'solid',
      validator: (value: string) => ['solid', 'outlined', 'text'].includes(value),
    },
    color: {
      type: String as PropType<ButtonColor>,
      default: 'primary',
      validator: (value: string) => [
        'primary', 'secondary', 'success',
        'error', 'warning', 'info'
      ].includes(value),
    },
    size: {
      type: String as PropType<ButtonSize>,
      default: 'medium',
      validator: (value: string) => ['small', 'medium', 'large'].includes(value),
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    loading: {
      type: Boolean,
      default: false,
    },
    startIcon: {
      type: String,
      default: '',
    },
    endIcon: {
      type: String,
      default: '',
    },
    className: {
      type: String,
      default: '',
    },
  },
  emits: ['click'],
  setup(props, { emit }) {
    const mapVariantToQuasar = computed(() => {
      const colorMap: Record<ButtonColor, string> = {
        primary: 'primary',
        secondary: 'secondary',
        success: 'positive',
        error: 'negative',
        warning: 'warning',
        info: 'info'
      };
      return colorMap[props.color as ButtonColor];
    });

    const mapSizeToQuasar = computed(() => {
      const sizeMap: Record<ButtonSize, string> = {
        small: 'sm',
        medium: 'md',
        large: 'lg'
      };
      return sizeMap[props.size];
    });

    const onClick = (event: MouseEvent) => {
      if (!props.disabled && !props.loading) {
        emit('click', event);
      }
    };

    return {
      mapVariantToQuasar,
      mapSizeToQuasar,
      onClick,
    };
  },
});
</script>

<style lang="scss" scoped>
.my-button {
  font-family: var(--ds-font-family, inherit);
  border-radius: var(--ds-button-radius, 4px);
  transition: all 0.2s ease;

  &--small {
    --ds-button-padding: var(--ds-spacing-2) var(--ds-spacing-3);
    --ds-button-font: var(--ds-text-sm);
  }

  &--medium {
    --ds-button-padding: var(--ds-spacing-3) var(--ds-spacing-4);
    --ds-button-font: var(--ds-text-base);
  }

  &--large {
    --ds-button-padding: var(--ds-spacing-4) var(--ds-spacing-5);
    --ds-button-font: var(--ds-text-lg);
  }

  &:hover:not(:disabled) {
    opacity: 0.9;
  }

  &:focus {
    outline: none;
    box-shadow: var(--ds-focus-ring);
  }
}
</style>
