import { App } from 'vue';
import { Quasar } from 'quasar';
import { createVuetify } from 'vuetify';
import * as components from 'vuetify/components';
import * as directives from 'vuetify/directives';
import 'vuetify/styles';

import MyButton from './components/Button/Button.vue';
import MySelect from './components/Select/Select.vue';
import MyBadge from './components/Badge/Badge.vue';
import MyBanner from './components/Banner/Banner.vue';
import MySkeleton from './components/Skeleton/Skeleton.vue';
import MyInputText from './components/InputText/InputText.vue';
import MyCard from './components/Card/Card.vue';

export { MyButton, MySelect, MyBadge, MyBanner, MySkeleton, MyInputText, MyCard  };

export type { ButtonVariant, ButtonSize, ButtonColor } from './components/Button/Button.vue';
export type { SelectVariant, SelectSize, SelectColor, SelectOption } from './components/Select/Select.vue';
export type { BadgeVariant, BadgeColor } from './components/Badge/Badge.vue';
export type { BannerVariant, BannerSize } from './components/Banner/Banner.vue';

const vuetify = createVuetify({
  components,
  directives,
});

export default {
  install: (app: App) => {
    app.use(Quasar, {
      config: {}
    });

    app.use(vuetify);

    app.component('MyButton', MyButton);
    app.component('MySelect', MySelect);
    app.component('MyBadge', MyBadge);
    app.component('MyBanner', MyBanner);
    app.component('MySkeleton', MySkeleton);
    app.component('MyInputText', MyInputText);
    app.component('MyCard', MyCard);
  }
};
