<!-- src/components/Banner/Banner.vue -->
<template>
    <q-banner
      :class="[
        'my-banner',
        `my-banner--${variant}`,
        className
      ]"
      :rounded="rounded"
      :dense="size === 'small'"
      :inline-actions="inlineActions"
    >
      <template v-if="icon" #avatar>
        <q-icon :name="icon" :color="mapColorToQuasar" />
      </template>

      <slot>{{ message }}</slot>

      <template v-if="$slots.action" #action>
        <slot name="action" />
      </template>
    </q-banner>
  </template>

  <script lang="ts">
  import { defineComponent, computed } from 'vue';
  import type { PropType } from 'vue';
  import { QBanner, QIcon } from 'quasar';

  export type BannerVariant = 'info' | 'success' | 'warning' | 'error';
  export type BannerSize = 'small' | 'medium';

  export default defineComponent({
    name: 'MyBanner',
    components: { QBanner, QIcon },
    props: {
      message: {
        type: String,
        default: '',
      },
      variant: {
        type: String as PropType<BannerVariant>,
        default: 'info',
        validator: (value: string) => ['info', 'success', 'warning', 'error'].includes(value),
      },
      icon: {
        type: String,
        default: '',
      },
      size: {
        type: String as PropType<BannerSize>,
        default: 'medium',
        validator: (value: string) => ['small', 'medium'].includes(value),
      },
      rounded: {
        type: Boolean,
        default: false,
      },
      inlineActions: {
        type: Boolean,
        default: false,
      },
      className: {
        type: String,
        default: '',
      },
    },
    setup(props) {
      const mapColorToQuasar = computed(() => {
        const colorMap: Record<BannerVariant, string> = {
          info: 'info',
          success: 'positive',
          warning: 'warning',
          error: 'negative'
        };
        return colorMap[props.variant];
      });

      return {
        mapColorToQuasar,
      };
    },
  });
  </script>

  <style lang="scss" scoped>
  .my-banner i.q-icon {
    padding-left: 5px;
    padding-top: 5px;
    padding-bottom: 5px;
  }

  .my-banner {
    border-radius: 7px !important;
    font-family: var(--ds-font-family, inherit);

    &--info {
      background-color: var(--ds-color-info-light, #e3f2fd);
    }

    &--success {
      background-color: var(--ds-color-success-light, #e8f5e9);
    }

    &--warning {
      background-color: var(--ds-color-warning-light, #fff3e0);
    }

    &--error {
      background-color: var(--ds-color-error-light, #ffebee);
    }
  }
  </style>
