<!-- src/components/Badge/Badge.vue -->
<template>
    <q-badge
      :color="mapColorToQuasar"
      :floating="floating"
      :rounded="rounded"
      :outline="variant === 'outlined'"
      class="my-badge"
      :class="[
        `my-badge--${variant}`,
        className
      ]"
    >
      <slot>{{ label }}</slot>
    </q-badge>
  </template>

  <script lang="ts">
  import { defineComponent, computed } from 'vue';
  import type { PropType } from 'vue';
  import { QBadge } from 'quasar';

  export type BadgeVariant = 'solid' | 'outlined';
  export type BadgeColor = 'primary' | 'secondary' | 'success' | 'error' | 'warning' | 'info';

  export default defineComponent({
    name: 'MyBadge',
    components: { QBadge },
    props: {
      label: {
        type: String,
        default: '',
      },
      variant: {
        type: String as PropType<BadgeVariant>,
        default: 'solid',
        validator: (value: string) => ['solid', 'outlined'].includes(value),
      },
      color: {
        type: String as PropType<BadgeColor>,
        default: 'primary',
        validator: (value: string) => [
          'primary', 'secondary', 'success',
          'error', 'warning', 'info'
        ].includes(value),
      },
      floating: {
        type: Boolean,
        default: false,
      },
      rounded: {
        type: Boolean,
        default: false,
      },
      className: {
        type: String,
        default: '',
      },
    },
    setup(props) {
      const mapColorToQuasar = computed(() => {
        const colorMap: Record<BadgeColor, string> = {
          primary: 'primary',
          secondary: 'secondary',
          success: 'positive',
          error: 'negative',
          warning: 'warning',
          info: 'info'
        };
        return colorMap[props.color as BadgeColor];
      });

      return {
        mapColorToQuasar,
      };
    },
  });
  </script>

  <style lang="scss" scoped>
  .my-badge {
    font-family: var(--ds-font-family, inherit);
    padding: 5px;
  }
  </style>
