<!-- src/components/Select/Select.vue -->
<template>
  <q-select
    :modelValue="modelValue"
    :options="options"
    :label="label"
    :color="mapColorToQuasar"
    :outlined="variant === 'outlined'"
    :borderless="variant === 'text'"
    :size="mapSizeToQuasar"
    :disable="disabled"
    :loading="loading"
    class="my-select"
    :class="[
      `my-select--${variant}`,
      `my-select--${size}`,
      className
    ]"
    @update:modelValue="onSelect"
  >
    <template v-if="$slots.prepend" #prepend>
      <slot name="prepend" />
    </template>

    <template v-if="$slots.append" #append>
      <slot name="append" />
    </template>
  </q-select>
</template>

<script lang="ts">
import { defineComponent, computed } from 'vue';
import type { PropType } from 'vue';
import { QSelect } from 'quasar';

export type SelectVariant = 'solid' | 'outlined' | 'text';
export type SelectSize = 'small' | 'medium' | 'large';
export type SelectColor = 'primary' | 'secondary' | 'success' | 'error' | 'warning' | 'info';

export interface SelectOption {
  label: string;
  value: any;
  disabled?: boolean;
}

export default defineComponent({
  name: 'MySelect',
  components: { QSelect },
  props: {
    modelValue: {
      type: [String, Number, Object, Array],
      default: null
    },
    options: {
      type: Array as PropType<SelectOption[]>,
      default: () => []
    },
    label: {
      type: String,
      default: ''
    },
    variant: {
      type: String as PropType<SelectVariant>,
      default: 'outlined',
      validator: (value: string) => ['solid', 'outlined', 'text'].includes(value)
    },
    color: {
      type: String as PropType<SelectColor>,
      default: 'primary',
      validator: (value: string) => [
        'primary', 'secondary', 'success',
        'error', 'warning', 'info'
      ].includes(value)
    },
    size: {
      type: String as PropType<SelectSize>,
      default: 'medium',
      validator: (value: string) => ['small', 'medium', 'large'].includes(value)
    },
    disabled: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    },
    className: {
      type: String,
      default: ''
    }
  },
  emits: ['update:modelValue', 'change'],
  setup(props, { emit }) {
    const mapColorToQuasar = computed(() => {
      const colorMap: Record<SelectColor, string> = {
        primary: 'primary',
        secondary: 'secondary',
        success: 'positive',
        error: 'negative',
        warning: 'warning',
        info: 'info'
      };
      return colorMap[props.color as SelectColor];
    });

    const mapSizeToQuasar = computed(() => {
      const sizeMap: Record<SelectSize, string> = {
        small: 'sm',
        medium: 'md',
        large: 'lg'
      };
      return sizeMap[props.size];
    });

    const onSelect = (value: unknown) => {
      emit('update:modelValue', value);
      emit('change', value);
    };

    return {
      mapColorToQuasar,
      mapSizeToQuasar,
      onSelect
    };
  }
});
</script>

<style lang="scss" scoped>
.my-select {
  font-family: var(--ds-font-family, inherit);
  border-radius: var(--ds-border-radius, 4px);
  transition: all 0.2s ease;

  &--small {
    --ds-select-height: 32px;
  }

  &--medium {
    --ds-select-height: 40px;
  }

  &--large {
    --ds-select-height: 48px;
  }
}
</style>
